
@keyframes progress {
0% {
    /*noinspection CssInvalidPropertyValue*/
    stroke-dasharray: 0 100;
}
}
[data-v-517179e5]::-webkit-scrollbar {
  width: 5px;
}
[data-v-517179e5]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(186, 186, 186);
}
[data-v-517179e5]::-webkit-scrollbar-thumb {
  background-color: #3c59b4;  /*thumb color */
  outline: 2px solid #fffffd;
}



